.header {
  width: 100%;
  height: 89px;
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 22px;
}

.header__content {
  display: flex;
  justify-content: space-between;
}

.header__logo img {
  padding-right: 32px;
}

.header__logo select {
  width: 127px;
  height: 17px;
  font-family: FiraSans;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  text-align: left;
  color: #ffffff;
  background: none;
}

.header__entry button {
  background: none;
  border: 2px solid #27c8ca;
  border-radius: 5px;
  padding: 15px 26px;
  margin-left: 5px;
}

.header__entry button:hover {
  background-image: linear-gradient(to bottom, #42d79f, #26c8cb);
  border: 2px solid #27c8ca;
}

.header__entry button:first-child {
  border: none;
}

.header__entry button p {
  font-family: FiraSans;
  font-size: 15px;
  font-weight: bold;
  font-style: normal;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
}

body {
  font-family: 'Fira Sans', sans-serif;
}

.container {
  width: 100%;
  max-width: 1140px;
  box-sizing: border-box;
  margin: 0 auto;
}

.footer {
  background-color: #43484d;
}

.footer__content {
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  padding-top: 45px;
  padding-bottom: 48px;
}

.footer__content .footer__block b {
  font-size: 13px;
  font-weight: 500;
  line-height: 2.69;
  text-align: left;
  color: rgba(255, 255, 255, 0.3);
}

.footer__content .footer__block a {
  text-decoration: none;
}

.footer__content .footer__block a p {
  font-size: 13px;
  font-weight: 500;
  line-height: 2.69;
  text-align: left;
  color: #ffffff;
}

.footer__content .footer__block a p img {
  padding-top: 33px;
}

.footer .footer__connection {
  padding-top: 35px;
  padding-bottom: 41px;
}

.footer .footer__connection .connection__text a {
  text-decoration: none;
  font-size: 13px;
  font-weight: normal;
  line-height: 2.69;
  color: #ffffff;
}

.footer .footer__connection .connection__text p {
  padding-top: 5px;
  font-family: FiraSans;
  font-size: 13px;
  font-weight: normal;
  color: rgba(255, 255, 255, 0.3);
}
