.wherefore {
  padding-top: 60px;
  position: relative;
  &__background {
    position: absolute;
    z-index: -1;
    left: 0; }
  &__title {
    h2 {
      font-size: 36px;
      font-weight: 500;
      color: #3c4450;
      text-align: center; }
    h3 {
      font-size: 22px;
      font-weight: 500;
      color: #707984;
      text-align: center;
      padding-bottom: 39px; } }
  &__block {
    padding-top: 54px;
    a {
      text-decoration: none;
      .wherefore__block__img {
        max-width: 126px;
        height: 126px;
        border-radius: 100px;
        box-shadow: 0px 11px 21px 0 rgba(20, 19, 23, 0.09);
        background-color: #ffffff;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center; } }
    &__text {
      padding-top: 41px;
      padding-left: 17px;
      h4 {
        font-size: 18px;
        font-weight: 700;
        color: #3c4450;
        text-align: left;
        padding-bottom: 11px; }
      p {
        font-size: 16px;
        font-weight: 500;
        line-height: 1.63;
        text-align: left;
        color: #707984; } } } }
