.header {
  width: 100%;
  height: 89px;
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 22px;
  &__content {
    display: flex;
    justify-content: space-between; }
  &__logo {
    img {
      padding-right: 32px; }
    select {
      width: 127px;
      height: 17px;
      font-family: FiraSans;
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      text-align: left;
      color: #ffffff;
      background: none; } }
  &__entry {
    button {
      background: none;
      border: 2px solid #27c8ca;
      border-radius: 5px;
      padding: 15px 26px;
      margin-left: 5px;
      &:hover {
        background-image: linear-gradient(to bottom, #42d79f, #26c8cb);
        border: 2px solid #27c8ca; }
      &:first-child {
        border: none; }
      p {
        font-family: FiraSans;
        font-size: 15px;
        font-weight: bold;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff; } } } }

